


:root{
    --yellow: #f5c32c;
    --orange: #fca61f;
    --black: #242d49;
    --gray: rgba(36, 45, 73, 0.65);
    --profileShadow: 0px 4px 17px 2px rgba(0, 0, 0, 0.25);
    --hrColor: #cfcdcd;
    --cardColor: rgba(255, 255, 255, 0.64);
    --buttonBg: linear-gradient(98.63deg, #f9a225 0%, #f95f35 100%);
    --inputColor: rgba(40, 52, 62, 0.07);
    --photo: #4CB256;
    --video: #4A4EB7;
    --location: #EF5757;
    --shedule: #E1AE4A;
}

.app{
    color: #242d49;
    color: var(--black);
    overflow: hidden;
    background-color: #f3f3f3;
    padding: 1rem 1rem;
}

.blurLeft{
    position: absolute;
    background-color:#a6ddf0 ;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    -webkit-filter: blur(72px);
            filter: blur(72px);
    top: -18%;
    right: 0;
}

.blurRight{
    position: absolute;
    background-color:#a6ddf0;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    -webkit-filter: blur(72px);
            filter: blur(72px);
    top: 36%;
    left: -8rem;
}

::-webkit-scrollbar{
    display: none;
}


.login {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    width: 100vw;
    height: 100vh;
    grid-gap: 2rem;
    gap: 2rem;
}

.loginLeft {

    display: flex;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}

.loginLeft img {
    width: 4rem;
    height: 4rem;
}

.loginInfo {
    display: flex;
    flex-direction: column;
}

.loginInfo h1 {
    font-size: 3rem;
    background-color: red;
    background-image: var(--buttonBg);
    background-size: 100%;
    background-repeat: repeat;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
    margin-bottom: 0px;
    display: flex;
    justify-content: center;
}

.loginInfo h6 {
    font-size: 0.85rem;
}

.loginRight {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--cardColor);
    border-radius: 10px;
    width: 35%;
}

.loginRight h3 {
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
}

.loginForm {

    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.loginPass{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.loginPass input{
    /* margin: 10px 10px; */
    padding: 18px;
    border: none;
    background-color: var(--inputColor);
    border-radius: 10px;
    outline: none;
    width: auto;
    margin:10px 10px;

}


.loginBtn{
    margin: 25px 0px;
}

.loginBtn button{
    font-weight: bold;
    padding: 8px 25px;
    border-radius: 10px;
    border: none;
    background: var(--buttonBg);
    color: white;
    margin-left: 10px;
}

.loginBtn button:hover{
    cursor: pointer;
    color: var(--orange);
    background: transparent;
    border: 2px solid var(--orange);
}


